import { graphql, withPrefix } from 'gatsby'
import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react'
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Box } from '@chakra-ui/react';
import { useWindowSize } from 'react-use';

import Layout from '../containers/Layout'
import Question from './question'
import Container from '../components/Container';
import { responsive } from '../components/ThemeProvider/theme';

const Q = ({ data, pageContext }) => {
  const { id } = pageContext
  const { length } = data.allQuestionsJson.nodes
  const [curentId, setCurrentId] = useState(id - 1)
  const slickRef = useRef()
  const loaded = useRef()
  const handleNext = useCallback((i) => {
    slickRef.current.slickGoTo(i + 1)
    setCurrentId(i + 1)
  }, [])
  const handlePrev = useCallback((i) => {
    slickRef.current.slickGoTo(i - 1)
    setCurrentId(i - 1)
  }, [])

  useEffect(() => {
    if (loaded.current) {
      const pathname = withPrefix(`/q/${curentId + 1}`)
      window.history.pushState(null, null, pathname)
      if (window.gtag) {
        window.gtag(`event`, `page_view`, { page_path: pathname })
      }
    }
    loaded.current = true
  }, [curentId])
  const { height } = useWindowSize()
  return (
    <Layout
      bg="white"
      title={`第${curentId + 1}題`}
      footerProps={{
        // bg: responsive('transparent', height > 630 ? 'transparent' : 'white'),
        maxWidth: responsive('auto', '375px'),
        mx: 'auto',
        py: responsive('1em', height > 700 ? 0 : '1em'),
        position: responsive('static', height > 700 ? 'absolute' : 'static'),
        left: '50%',
        transform: responsive('none', height > 700 ? 'translateX(-50%)' : 'none'),
        bottom: '2em',
      }}
      headerProps={{
        mt: "1.5em"
      }}
      height={responsive('100%', height > 811 ? '812px' : '100%')}
      measureHeight={height}
    >
      <Container maxWidth={responsive('auto', '375px')} pt="1em" px="0">
        {useMemo(() => (
          <Slider
            accessibility={false}
            arrows={false}
            dots={false}
            infinite={false}
            touchMove={false}
            draggable={false}
            ref={slickRef}
            speed={500}
            initialSlide={curentId}
          >
            {data.allQuestionsJson.nodes.map((d, i) => (
              <Box bg="white" key={d.id} px="1.875em">
                <Question
                  data={d}
                  onNext={() => handleNext(i)}
                  onPrev={() => handlePrev(i)}
                  pageContext={{ id: d.id, length, isLast: i === length - 1 }}
                />
              </Box>
            ))}
          </Slider>
        ), [curentId, data.allQuestionsJson.nodes, handleNext, handlePrev, length])}
      </Container>
    </Layout>
  )
}

export default Q

export const query = graphql`
  query QuestionPageQuery {
    allQuestionsJson {
      nodes {
        id
        question
        options
      }
    }
  }
`
